//import FillBaskets from "../functions/FillBasket";
import Grid from '@mui/material/Grid';
// import ProductFunctions from "../functions/FillProducts"; 
import Message from "../components/Message";

import Card2 from '../components/Card2';
import ProductOperations from "../services/Product-operations";
//import Container from '@mui/material/Container';
import {useState} from 'react';
import Comp1 from './Display-page1';
import {  Left } from '../components/Header';
import Head from '../components/Head';
import { Button } from '@mui/material';
import GooglePayButton from '@google-pay/button-react';
import { Google } from '@mui/icons-material';
// import Product from '../model/product';

const Products=await ProductOperations.loadproduct();

var gender='Any';

function DisplayFunction(){

const[count,setCount]=useState(0);
const Update=(val,pizza_id)=>{
       console.log('Update function called');
       const pizza = Products.find(currentProduct=>currentProduct.id===pizza_id);
       if(val==='+')pizza.quantity++;
       else pizza.quantity--;
       console.log(pizza.quantity);
       setCount(count+1);
}
    var total=0.0;
    return (
        <>  
        <Grid container spacing={1} margin={2}>
        <Grid item xs={2}>
        <img src="https://s3-eu-west-1.amazonaws.com/tpd/logos/47783f580000640005020e95/0x0.png" height="100px" className="src" />
        </Grid>
        <Grid container xs={8} >
        <Message classname="Kala Kutta"/>
        </Grid>
        </Grid>
        <Grid container spacing={1} margin={2} >
            <Grid container item xs={9}>
            <Grid container item xs={12}>
            <Head classname="Products"/>
            </Grid>
            
            <Left fn={Update}/>
            </Grid>
            <Grid item xs={2}>
            <Head classname="Basket"/> 
            <dir></dir>
                <Grid container rowSpacing={1} sx={{background:'#403234'}}>
                    {Products.map(prod => {
                    total=total+parseFloat(prod.price)*prod.quantity;
                    if(prod.quantity)return (<Card2 id={prod.id} name={prod.name} quantity={prod.quantity} price={prod.price} url={prod.url} fn={Update}/>)})}
                </Grid>
                <Message classname="Total : " val={total.toFixed(2)} heading={5}/>
                <Message classname="Payable Amount : " val={(total*1.18).toFixed(2)} heading={5}/>
                <GooglePayButton
  environment="TEST"
  paymentRequest={{
    apiVersion: 2,
    apiVersionMinor: 0,
    allowedPaymentMethods: [
      {
        type: 'CARD',
        parameters: {
          allowedAuthMethods: ['PAN_ONLY', 'CRYPTOGRAM_3DS'],
          allowedCardNetworks: ['MASTERCARD', 'VISA'],
        },
        tokenizationSpecification: {
          type: 'PAYMENT_GATEWAY',
          parameters: {
            gateway: 'example',
            gatewayMerchantId: 'exampleGatewayMerchantId',
          },
        },
      },
    ],
    merchantInfo: {
      merchantId: '12345678901234567890',
      merchantName: 'Demo Merchant',
    },
    transactionInfo: {
      totalPriceStatus: 'FINAL',
      totalPriceLabel: 'Total',
      totalPrice: '100.00',
      currencyCode: 'USD',
      countryCode: 'US',
    },
  }}
  onLoadPaymentData={paymentRequest => {
    console.log('load payment data', paymentRequest);
  }}/>
    </Grid>
    </Grid>                
    </>
    );
} 
export default DisplayFunction;