import Button from '@mui/material/Button';
const Buttons=({val,name,fn,pizza_id})=>{
    function fun1(){
        console.log('clicked pizza id  : ',pizza_id);
        fn(val,pizza_id);
    }
    if(val==='+')return(
        <div><Button variant="outlined" onClick={fun1} id={pizza_id}>{name}</Button></div>
    )
    else return(
        <div><Button variant="outlined" onClick={fun1} id={pizza_id}>{name}</Button></div>
    )
}
export default Buttons;