import Networkcall from "./Api-client";
import Product from "../model/Product.jsx";
const ProductOperations={
    
async loadproduct(){
        const data=await Networkcall();
        console.log('data : ',data);
        const products=data['products'];
        console.log('Product',products);
        const productArray=products.map(prod=>{
            const curr_prod=new Product(prod.id, prod.name,
                prod.price, prod.image_url,prod.gender);
            return curr_prod;
        });
        console.log('Product array : ',productArray);
        return productArray;
    }
, 
sort(){

}

}

export default ProductOperations;